.ctnr {
  position: relative;
  /*width: 350px;
  height: 350px;*/
  margin-top: 20px;
  margin-left: 20px;
}
.ctnr-caption {
  /*width: 350px;*/
  /*height: 350px;*/
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: transparent;
  visibility: hidden;
  opacity: 0;
}
.ctnr:hover .ctnr-caption {
  visibility: visible;
  -webkit-transition: .5s opacity linear, .3s transform linear;
  transition: .5s opacity linear, .3s transform linear;
  opacity: 1.0;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}
.ctnr-mask {
  position: absolute;
  top: 0;
  left: 0;
  /*width: 350px;
  height: 350px;*/
  z-index: 1;
  background-color: transparent;
}
.ctnr:hover .ctnr-mask {
  opacity: 0.65;
  -webkit-transition: .5s opacity ease-in-out, .5s background-color ease-in-out;
  transition: .5s opacity ease-in-out, .5s background-color ease-in-out;
  background-color: black;
}
